var MyApp = angular.module("MyApp", ["ui.router"]);

MyApp.config(["$stateProvider", "$urlRouterProvider", function($stateProvider, $urlRouterProvider) {
  $urlRouterProvider.otherwise("/home");

  $stateProvider
    .state("main", {
      url: "/",
      templateUrl: "templates/main/main.html",
      controller: "MainController",
      controllerAs: "main"
    })
    .state("main.home", {
      url: "home",
      templateUrl: "templates/home/home.html",
    })
    .state("main.login", {
      url: "login",
      templateUrl: "templates/login/login.html",
    })
    .state("main.chatroom", {
      url: "chatroom",
      templateUrl: "templates/chat/chatroom.html",
    })
    .state("stuff", {
      url: "/stuff",
      templateUrl: "templates/stuff/stuff.html",
      controller: "StuffController",
      controllerAs: "stuffCtrl"
    })
}]);

MyApp.service("ApiService", function() {

  this.syncMethod = function() {
    return 0;
  }

  this.getThings = function() {
    return $http.get("/thing")
      .then(function (response) {
        return response.data;
      })
      .catch(function (err) {
        console.error("Error ApiService getThings ", err);
        return {};
      })
  }

})
MyApp.controller("MainController", ["$scope", function($scope) {
  $scope.doStuff = function() {
    return "stuff";
  }
}])

MyApp.controller("LoginController", function() {

})

MyApp.directive("navTop", function() {
  return {
    templateUrl: "templates/layout-directives/nav-top2.html",
  };
});

MyApp.controller("OtherController", ["$scope", function($scope) {
  $scope.filter = true;
  $scope.toggleFilter = function() {
    $scope.filter = !$scope.filter;
  }
}])

MyApp.controller("StuffController", function() {

})
